<template>
<AppLoad
  v-if="page.loading"
  theme="dark"
  />
<UiPanel id="HomePage" class="UiTheme_dark" v-else>
  <Slider
    v-if="!page.loading"
    :slides="acf.panels"
    :settings="{
      breakpoints: {
        1088: {
          direction: 'horizontal',
          freeMode: false
        }
      },
      direction: 'vertical',
      freeMode: true,
      freeModeSticky: true,
      hashNavigation: {
        watchState: true,
        replaceState: true
      },
      keyboard: true,
      lazy: {
        loadPrevNext: true,
        loadOnTransitionStart: true,
      },
      // preloadImages: false,
      mousewheel: {
        releaseOnEdges: true,
        sensitivity: 1
      },
      pagination: {
        type: 'bullets'
      },
      parallax: true,
      speed: 400
    }"/>

</UiPanel>
</template>

<script>
import API from "@/VuePress/mix/API"
import Slider from "@/components/modules/Slider"

import UiPanel from "@/components/UI/Panel"

export default {
  name: "HomePage",
  mixins:[ API ],
  components:{ UiPanel, Slider },
  created() {
    this.$log(this.$root.classes,'')
  },
  asyncComputed:{
    page:{
      default:{ loading:true },
      async get(){
        if(!( this.API && this.endpoint )) return {loading:true}

        let
        err = false,
        xhr = await this.endpoint.get().catch(e=>( err = e ))

        if (err) return { error:err }
        return xhr
      }
    }
  },
  computed:{
    endpoint(){
      if( !this.API ) return false
      return this.API.namespace('ws/v1').front()
    },
    acf(){
      if( !this.page.loading )
        return this.page.acf
    }
  },
}
</script>

