ring: map-get($breakpoint, 'query');\n  $query-fallback: map-get($breakpoint, 'fallback');\n\n  $private-breakpoint-context-holder: map-get($breakpoint, 'context holder') !global;\n  $private-breakpoint-query-count: map-get($breakpoint, 'query count') !global;\n\n  // Allow for an as-needed override or usage of no query fallback.\n  @if $no-query != false {\n    $query-fallback: $no-query;\n  }\n\n  @if $query-fallback != false {\n    $context-setter: private-breakpoint-set-context('no-query', $query-fallback);\n  }\n\n  // Print Out Query String\n  @if not breakpoint-get('no queries') {\n    @media #{$query-string} {\n      @content;\n    }\n  }\n\n  @if breakpoint-get('no query fallbacks') != false or breakpoint-get('no queries') == true {\n\n    $type: type-of(breakpoint-get('no query fallbacks'));\n    $print: false;\n\n    @if ($type == 'bool') {\n      $print: true;\n    }\n    @else if ($type == 'string') {\n      @if $query-fallback == breakpoint-get('no query fallbacks') {\n        $print: true;\n      }\n    }\n    @else if ($type == 'list') {\n      @each $wrapper in breakpoint-get('no query fallbacks') {\n        @if $query-fallback == $wrapper {\n          $print: true;\n        }\n      }\n    }\n\n    // Write Fallback\n    @if ($query-fallback != false) and ($print == true) {\n      $type-fallback: type-of($query-fallback);\n\n      @if ($type-fallback != 'bool') {\n        #{$query-fallback} & {\n          @content;\n        }\n      }\n      @else {\n        @content;\n      }\n    }\n  }\n\n  @include private-breakpoint-reset-contexts();\n}\n\n\n@mixin mq($query, $no-query: false) {\n  @include breakpoint($query, $no-query) {\n    @content;\n  }\n}\n","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"~@/styles/theme/colors\";\n@import \"~@/styles/theme/breaks\";\n\n.app.route_HomePage {\n  @include Break( max-width Breaks(3) ){\n    #AppNav {\n      background-color: rgba(#FAFAFA,.75);\n      // background-image:\n      //   linear-gradient( to bottom, , rgba(#FAFAFA,.25) 75% ),\n      //   linear-gradient( to left, rgba(#FAFAFA,.5), transparet 25%, transparet 75%, rgba(#FAFAFA,.5) )\n      //   !important;\n      // color: #FAFAFA !important;\n      box-shadow:\n        0 .3em 1em rgba(Color(dark),.1),\n        0 1px 0 rgba(#FFF,.7);\n        ;\n      &:before {\n        z-index: -1;\n        content: '';\n        position: absolute;\n        top: -1px;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        backdrop-filter: blur(9px);\n        -webkit-backdrop-filter: blur(9px);\n        pointer-events: none;\n      }\n    }\n  }\n}\n\n#HomePage {\n\n  $top-offset: 6rem;\n  $top-offset--M: 4rem;\n  $left-offset: 11rem;\n  \n  & {\n    z-index: 1;\n    position: relative;\n    height: 100vh;\n    color: Color(light);\n    @include Break( max-width Breaks(4) ){\n      margin-top: -$top-offset--M;\n    }\n  }\n  +#AppFooter {\n    display: none;\n  }\n  .swiper {\n    &-container {\n      @include Break( min-width Breaks(4) ){\n        position: relative;\n        height: 100%;\n        // padding-top: $top-offset;\n        // margin-top: -$top-offset;\n      }\n      @include Break( max-width Breaks(4) ){\n        position: absolute;\n        height: 100%;\n        // margin-top: -$top-offset--M;\n      }\n    }\n    &-slide {\n      padding: 1.5rem 2rem;\n      @include Break( min-width Breaks(4) ){\n        padding: 9rem 4rem 4rem;\n        padding-left: $left-offset;\n      }\n    }\n    &-pagination {\n      &-bullets {\n        display: flex;\n        flex-flow: nowrap column;\n        justify-content: stretch;\n        align-items: center;\n        @include Break( min-width Breaks(4) ){\n          height: 50vh;\n          right: unset !important;\n          left: $left-offset / 2;\n          top: 50%;\n          top: calc(50% + 1.5rem);\n        }\n        @include Break( max-width Breaks(4) ){\n          flex-flow: nowrap row;\n          height: 8px;\n          width: 88vw;\n          right: 6vw !important;\n          left: unset;\n          bottom: 1.5rem;\n        }\n      }\n      &-bullet {\n        all: unset;\n        flex: 1;\n        width: 8px;\n        height: 8px;\n        margin: 0 !important;\n        background: rgba(Color(light),.25);\n        transition: .3s ease;\n        & + * {\n          @include Break( min-width Breaks(4) ){\n            margin-top: 2px !important;\n          }\n          @include Break( max-width Breaks(4) ){\n            margin-left: 2px !important;\n          }\n        }\n        &-active {\n          position: relative;\n          background: Color(theme);\n          box-shadow:\n            2px 0 0 Color(theme),\n            -2px 0 0 Color(theme);\n          @include Break( max-width Breaks(4) ){\n            box-shadow:\n              0  2px 0 Color(theme),\n              0 -2px 0 Color(theme);\n          }\n          &:after {\n            content: \"Slide\";\n            position: absolute;\n            text-align: center;\n            width: 100%;\n            left: 0;\n            bottom: 100%;\n            @include Break( min-width Breaks(4) ){\n              top: 50%;\n              left: unset;\n              bottom: unset;\n              right: 100%;\n              transform: translateY(-50%) rotate(-90deg);\n              margin-right: .5em;\n              text-align: right;\n            }\n            display: none !important;\n          }\n        }\n      }\n    }\n  }\n}\n","$c-black: #0e0e0e;\n$c-matte: invert($c-black,99.5%);\n$c-theme: #BA9454;\n$c-alt:   #D21034;\n\n$colors:(\n  dark:    $c-black, // #0E0E0E\n  slate:   lighten($c-black,15%), // #323232,\n  silver:  lighten($c-black,67%), // #B8B8B8\n  cream:   lighten(desaturate(adjust-hue($c-theme,7deg),14),42), // #F5F3ED,\n  creamDark: #BFB4A0,\n  light:   mix( white, $c-matte, 69% ), // #F6F4EE\n  matte:   $c-matte, // #F0F0F0\n  theme:   $c-theme, // #BA9454\n  alt:     $c-alt,\n);\n\n@function Color($key) {\n  $color: map-get($colors,$key);\n  @return $color;\n}"],"sourceRoot":""}]);

